<template>
  <div id="app">

    <div id='header'>
      <nav id='nav'>

        <router-link to="/hello">about</router-link>

      </nav>
    </div>

    <router-view/>

    <p>{{users}}</p>
    <ul>
      <li v-for='user in users'>{{user.name}}</li>
    </ul>
    <p>{{xx}}</p>

    <button @click='add()'>click</button>

  </div>
</template>

<script>
import { db } from './firebase'

export default {
  name: 'App',
  components: {

  },
  firestore () {
    return {
      users: db.collection('users'),
      xx: db.collection('users').doc('user1')
    }
  },
  methods: {
    add() {
      db.collection('users').doc('user1').set({
        name: 'AAA'
      }, {merge: true})
    }
  }
}
</script>

<style lang="sass">

  @import '@/themes/bit/main.sass'

</style>



